* {
  /* @apply text-prime-white-2 fill-prime-white-2; */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;








@layer components {
    
   
    body{
       @apply font-Lexend font-light text-prime-dark bg-prime-white;
       line-height: 1.1;
       overflow-x: hidden;
    }
    img{
        @apply object-cover origin-center
    }
    svg > path{
        @apply fill-prime-dark;
    }
    .hero-title{
        line-height: 0.9;
        font-size: clamp(3vw, 18vw, 25vw); 
        @apply font-black font-PlayfairDisplay  uppercase;
    }
    .hero-title-2{
        font-size: clamp(3rem, 8vw, 20rem);
        line-height: 0.9;
        @apply font-black font-PlayfairDisplay  uppercase;
    }
   
    .hero-width-2{
        width: clamp(45vw, 55vw, 60vw); 
    }
     .hero-title-3{
        font-size: 1.6rem; 
        @apply font-black font-PlayfairDisplay  uppercase;
    }
    .internal-padding{
        padding: clamp(0.9vw, 3vw, 6vw);
    }
    .hero-gap{
        gap: clamp(2vw, 6vw, 10vw);
    }
    .super-gap{
        gap: clamp(4vw, 12vw, 20vw);
    }
    .hero-padding{
        padding: clamp(2vw, 6vw, 10vw);
      }
    .hero-margin{
        padding: clamp(2vw, 6vw, 10vw);
      }
    
      .entry-element{
        translate: 0% 100%;
        rotate: z 3deg;
        transform-origin: 0% 100%;
      }
      .entry-element-left-scrub{
        translate: -100% 0%;
      }
      .entry-element-right-scrub{
        translate: 100% 0%;
      }
      .entry-element-rev{
        translate: 0% -100%;
        rotate: z 3deg;
        transform-origin: 0% 100%;
      }
      .entry-fade{
        opacity: 0;
      }
      .onEntry-effect{
        translate: 0% 100%;
        rotate: z 3deg;
        transform-origin: 0% 100%;
      }
      .onEntry-effect-right{
        translate: 100% 0%;
      }
    .super-padding{
        padding-right: clamp(6vw, 16vw, 30vw);
        padding-left: clamp(6vw, 16vw, 30vw);
        padding-top: clamp(3vw, 8vw, 15vw);
        padding-bottom: clamp(3vw, 8vw, 15vw);
    }
    .image-random-move{
        aspect-ratio: 1;
        translate: 0 9%;
        scale:1.2;
    }
  
   .spacer{
    height: 20vh;
   }
    .hero-section{
        min-height: 100vh;
        @apply relative;
    }
    .hero-text-group{
        @apply hero-title;
    }
    .hero-text-group > div{
        @apply overflow-hidden
    }
    
    .hero-content{
        @apply hero-padding;
        padding-top: clamp(13vw, 14vw, 15vw);
    }
    .hero-spacer{
        height: 17vh;
    }
   
    
    .hero-about{
       @apply internal-padding;
        padding-top: 10%;
        padding-bottom: 10%;
        @apply  flex flex-col justify-end;
        
    }
 
    .hero-about > div{
        @apply overflow-hidden;
    }
    .navlinks{
        rotate: z 4deg;
        translate: 0% 100%;
        padding: 4px;
    }
    .suglinks{
        rotate: z 4deg;
        translate: 0% 100%;
    }
    .menuFrame{
        @apply hero-padding hero-title-2 h-screen w-screen bg-prime-white text-prime-dark flex flex-col uppercase  justify-center items-center fixed z-10 right-0 top-0 font-black font-PlayfairDisplay  m-auto;
    }
    .sgg-list{
        @apply p-4 w-full flex flex-col items-center justify-center;
    }
    
    .search-section{
        @apply flex flex-col  items-center uppercase  m-auto;
    }
     .searchFrame{
        padding-top: 20vh;
        @apply h-screen bg-prime-white w-screen fixed z-10 right-0 top-0 overflow-y-scroll;
    }
   .hero-about h2{
       @apply w-3/4 sm:w-2/6;
   }
   .searchFrame,.menuFrame{
    transform-origin: 0% 100%;
    scale:1.3;
    translate: 0 -100vh;
    rotate: z -2deg;
    }
    .hero-img{
        z-index: -1;
        @apply w-full h-full object-cover absolute top-0 ;
    }
    .page-posi{
        right:clamp(1vw,3vw,7vw);
        z-index: 1;
        @apply w-8 fixed -translate-y-1/2 overflow-hidden;
        top:50vh;
    }
    .hug-element{
        @apply py-3 flex items-center text-xs tracking-widest	 text-gray-400 uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 after:flex-[1_1_0%] after:border-t after:border-primary-500 after:ms-4;
    }
    
    .posi-path{
        @apply fill-prime-dark duration-500 hover:fill-primary-500;
    }
    .active-posi{
        @apply fill-primary-500;
    }
    
    .nav-bar {
    z-index:12;
     width: 100%;
     height: 20vh;
     padding: 7vh;
     @apply  flex flex-wrap items-center justify-between  fixed top-0 ;
    }
    
    input{
            
        @apply appearance-none block w-full rounded-prime  bg-white border  py-3 px-4 leading-tight focus:outline-none focus:bg-info-100 mx-auto h-11;
    }
    
    .button-primary{
        width: auto;
        padding: 3px 10px;
        @apply   font-medium w-auto rounded-prime text-prime-white text-sm py-3 px-4 text-center me-2 mb-2 transition-all duration-500 bg-gradient-to-tl  bg-size-200 bg-pos-0 hover:bg-pos-100 from-primary-100 via-primary-300 to-primary-500 hover:text-primary-800;
    }
    .button-neutral{
        min-width: 90px;
        padding: 3px 10px;
       @apply   font-medium rounded-md text-sm px-5 py-2 text-center me-2 mb-2 transition-all duration-500 bg-gradient-to-tl  bg-size-200 bg-pos-0 hover:bg-pos-100 from-blue-200 via-gray-200 to-blue-200;
    }
    .button-delete{
        width: auto;
        padding: 3px 10px;
       @apply   font-medium  text-prime-white rounded-md text-sm px-5 py-2 text-center me-2 mb-2 transition-all duration-500 bg-gradient-to-tl  bg-size-200 bg-pos-0 hover:bg-pos-100 from-danger-200 via-danger-400  to-danger-300;
    }
    .normal-bold{
        @apply font-normal text-lg capitalize;
    }
    .zlist{
        @apply flex flex-col w-4/5 sm:w-1/2 py-5;
    }
    a{
      @apply  duration-700 text-prime-dark hover:text-primary-500;
    }
    .internal-links{
        @apply   py-3 px-4 ;
    }
    
    /* .internal-links::after {
        content: url("../svg/arrow.svg");
        @apply pl-1 duration-300  hover:pl-3;
      } */
    .nav-bar-logo{
        @apply   h-full flex justify-center items-center;
    }
    .overflowhidden{
        overflow: hidden;
    }
    .nav-bar-logo a{
        @apply h-full;
    }
    .nav-bar-logo a img{
        @apply h-full;
    }
    .nav-bar-links{
        width: auto;
        @apply  justify-center items-center h-full;
    }
    .nav-bar-links ul li {
        padding-left: 4vw;
    }
   .direction-section{
    @apply relative overflow-hidden;
   }
   .title-wrapper{
    @apply hero-padding;
   }
   .mid-title-wrapper{
       @apply overflow-hidden hero-title-2;

   }
   .feature-with-icon{
    @apply flex items-center overflow-hidden;
   }
   .feature-with-icon svg{
    @apply mr-5 w-6 h-auto;
   }
  
   .feature-title{
    @apply hero-title-3 text-center ;
   }
 
   .preloader{
    position:fixed;
            top:0;
            background:#f1f2f3;
            width:100%;
            height:100vh;
            z-index: 50;
            display:flex;
            justify-content:center;
            flex-direction:column;
            
            align-items:center;
            transition:0.5s ease all;
   }
   .logofile{
    @apply w-1/3 sm:w-1/5;
    position:fixed;
right:50vw;
translate: 50% 0%;
   }
   .loader{
    @apply w-6/12 sm:w-2/6;
   }
    .direction-svg-exo{
        z-index: -1;
        pointer-events: none;
        position: absolute;
        top:clamp(75vw, 77vw, 79vw); 
        right:0;
        translate:  50% 0;
        @apply rotate-45 hero-width-2;
    }
    .direction-svg-left{
        z-index: -1;
        pointer-events: none;
        position: absolute;
        top:clamp(75vw, 77vw, 79vw); 
        left:0;
        translate:  -50% 0;
        @apply rotate-45 hero-width-2;
    }
    .direction-svg-right{
        z-index: -1;
        pointer-events: none;
        position: absolute;
        top:clamp(10vw, 16vw, 25vw); 
        right:0;
        translate:  50% 0;
        @apply rotate-45 hero-width-2;
    }
    
  }
@layer utilities {
   
}

